html {
    height: 100%;
    margin: 0;
    padding: 0;
}

body {
            font-family: Arial, sans-serif;
            background-color: #f4f4f4;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100vh;
            margin: 0;
        }

        #login-container {
            background-color: #fff;
            padding: 20px;
            border-radius: 5px;
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
            text-align: center;
            width: 300px;
        }

        #login-container h2 {
            margin: 0 0 20px 0;
            color: #333;
        }

        #login-container input[type="email"],
        #login-container input[type="password"] {
            width: 100%;
            padding: 10px;
            margin-bottom: 15px;
            border: 1px solid #ddd;
            border-radius: 4px;
            box-sizing: border-box;
        }

        #login-container button {
            width: 100%;
            padding: 10px;
            background-color: #007bff;
            color: white;
            border: none;
            border-radius: 4px;
            cursor: pointer;
        }

        #login-container button:hover {
            background-color: #0056b3;
        }

        #login-error {
            margin-top: 15px;
        }


/*        Leaflet Styling       */
#map {
    height: 100%;
}

/* Tooltip styling */
.leaflet-tooltip {
  font-size: 16px;
  font-weight: bold;
  color: rgb(139, 0, 0) !important;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 4px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

